.avatar {
  display: inline-block;
  vertical-align: top;
  position: relative;
  font-size: 40px;
  line-height: 1;
}
.avatar-img {
  overflow: hidden;
  border-radius: 50em;
  width: 1em;
  vertical-align: top;
  line-height: 1;
}
.avatar-badge {
  position: absolute;
  right: 0;
  bottom: 0;
  background: #b2b2b2;
  width: 15px;
  height: 15px;
  border: solid 2px #fff;
  border-radius: 15px;
}
.avatar-badge.type-away {
  background: #b2b2b2;
}
.avatar-badge.type-online {
  background: #0084ff;
}
.bubble {
  display: inline-block;
  background: #f1f0f0;
  border-radius: 20px;
  text-align: left;
  max-width: 75%;
  margin-bottom: 0.2em;
  overflow: hidden;
}
.bubble-text {
  padding: 0.5em 0.8em;
  line-height: 1.4;
}
.bubble-image {
  max-width: 100%;
}
.bubbleGroup .bubble-wrap.is-their .bubble {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.bubbleGroup .bubble-wrap.is-their:first-child .bubble {
  border-top-left-radius: 20px;
}
.bubbleGroup .bubble-wrap.is-their:last-child .bubble {
  border-bottom-left-radius: 20px;
}
.bubbleGroup .bubble-wrap.is-our .bubble {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.bubbleGroup .bubble-wrap.is-our:first-child .bubble {
  border-top-right-radius: 20px;
}
.bubbleGroup .bubble-wrap.is-our:last-child .bubble {
  border-bottom-right-radius: 20px;
}
.bubble-wrap {
  line-height: 1;
}
.bubble-wrap.is-our {
  text-align: right;
}
.bubble-wrap.is-our .bubble {
  background: #0084ff;
  color: #fff;
  margin-left: auto;
}
.bubbleGroup {
  position: relative;
  margin: 0.3em 0;
}
.bubbleGroup.has-avatar {
  padding-left: 48px;
}
.bubbleGroup-avatar {
  position: absolute;
  bottom: 0.3em;
  left: 0;
}
.bubbleGroup-avatar .avatar {
  font-size: 40px;
}
.button {
  padding: 0.5em;
  color: #0084ff;
}
.genericElement {
  display: inline-block;
  vertical-align: top;
  border: solid 1px #ebebeb;
  border-radius: 6px;
  overflow: hidden;
  width: 15em;
  max-width: 80vw;
  margin-bottom: 0.3em;
}
.genericElement-image {
  height: 0;
  padding-top: 52.35602%;
  background-position: center center;
  background-size: cover;
}
.genericElement-text {
  padding: 0.5em 0.75em;
  font-size: 0.95em;
  line-height: 1.4;
}
.genericElement-title {
  color: #000;
  font-weight: 600;
}
.genericElement-subtitle {
  color: #818181;
}
.genericElement-itemUrl {
  color: #b2b2b2;
}
.genericElement-buttons .button {
  border-top: solid 1px #ebebeb;
  text-align: center;
}
.genericElement:first-child {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}
.genericElement:last-child {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}
.genericElementList {
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  white-space: nowrap;
}
.genericElementList > * {
  white-space: initial;
}
.persistentMenu {
  background: #fff;
}
.persistentMenu-head {
  background: #f9f9f9;
  border-bottom: solid 1px rgba(0,0,0,0.15);
  text-align: center;
  position: relative;
}
.persistentMenu-title,
.persistentMenu-closeBtn {
  padding: 0.5em;
}
.persistentMenu-title {
  font-weight: 600;
}
.persistentMenu-closeBtn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  color: #037aff;
}
.persistentMenu-elements {
  padding: 0.25em 0;
}
.persistentMenu-element .button {
  display: block;
  text-align: center;
}
.screen .persistentMenu {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.screen .persistentMenu-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.2);
}
.quickReply {
  display: inline-block;
  color: #0084ff;
  border: solid 1px;
  line-height: 1;
  padding: 0.5em 0.75em;
  border-radius: 2em;
}
.quickReplyList {
  text-align: center;
  width: 100%;
  white-space: nowrap;
  overflow-y: auto;
}
.receipt {
  display: inline-block;
  vertical-align: top;
  border: solid 1px #ebebeb;
  border-radius: 20px;
  overflow: hidden;
  width: 15em;
  max-width: 90vw;
  margin-bottom: 0.3em;
  padding: 0 0.75em;
}
.receipt-head,
.receipt-body,
.receipt-foot {
  padding: 0.5em 0;
}
.receipt-head {
  border-bottom: solid 1px #ebebeb;
  color: #929292;
}
.receipt-body {
  font-size: 0.95em;
  line-height: 1.4;
}
.receipt-foot {
  border-top: solid 1px #ebebeb;
}
.receipt-foot-label {
  float: left;
  color: #929292;
}
.receipt-foot-sum {
  float: right;
  font-weight: 600;
}
.receipt-foot:after {
  display: block;
  content: '';
  clear: both;
}
.receipt-key {
  margin-top: 0.5em;
  color: #929292;
}
.receipt-value {
  font-weight: 600;
}
.receiptElement {
  margin-bottom: 0.3em;
}
.receiptElement:after {
  display: block;
  content: '';
  clear: both;
}
.receiptElement-image {
  width: 5em;
  height: 5.5em;
  background-size: cover;
  background-position: center center;
  border: solid 1px #ebebeb;
  float: left;
  margin-right: 0.5em;
}
.receiptElement-desc {
  line-height: 1.2;
}
.receiptElement-title {
  font-weight: 600;
}
.receiptElement-subtitle {
  color: #929292;
  font-size: 0.95em;
  margin-top: 0.1em;
}
.screen {
  display: inline-block;
  width: 375px;
  max-width: 100%;
  height: 520px;
  background: #fff;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
}
.screen.withShadow {
  box-shadow: 0 1px 2px rgba(0,0,0,0.2), 0 2px 10px rgba(0,0,0,0.2);
}
.screen .conversation {
  padding: 1em;
  max-height: 100%;
  overflow-y: auto;
}
.threadList {
  padding: 0.75em;
}
.threadListElement {
  position: relative;
  margin-bottom: 0.5em;
  line-height: 1;
}
.threadListElement-avatar {
  position: absolute;
  top: 0;
  left: 0;
}
.threadListElement-avatar .avatar {
  display: block;
  font-size: 56px;
}
.threadListElement-info {
  padding-top: 0.5em;
  min-height: 56px;
  margin-left: 67.2px;
  border-bottom: solid 1px #ebebeb;
  position: relative;
  line-height: 1.4;
}
.threadListElement-lead {
  color: #929292;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.9em;
  margin-right: 2em;
}
.threadListElement-timestamp {
  float: right;
  font-size: 0.8em;
  padding-top: 0.3em;
}
.threadListElement-status {
  position: absolute;
  top: 2.4em;
  right: 0;
  width: 0.5em;
  height: 0.5em;
  background: #ebebeb;
  border-radius: 0.5em;
}
.threadListElement.is-unread .threadListElement-label,
.threadListElement.is-unread .threadListElement-lead {
  font-weight: 600;
  color: #000;
}
.threadListElement.is-unread .threadListElement-timestamp .timestamp {
  color: #0084ff;
}
.timestamp {
  color: #b2b2b2;
}
.timestamp.view-block {
  display: block;
  text-align: center;
  margin: 0.5em 0;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
@-ms-viewport {
  width: device-width;
}
html {
  font-size: 16px;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: #333;
  background-color: #fff;
}
[tabindex="-1"]:focus {
  outline: none !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}
p {
  margin-top: 0;
  margin-bottom: 1rem;
}
abbr[title],
abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted #333;
}
address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}
ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}
ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}
dt {
  font-weight: bold;
}
dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}
blockquote {
  margin: 0 0 1rem;
}
a {
  color: #11e;
  text-decoration: none;
}
a:not([href]) {
  color: inherit;
  text-decoration: none;
}
a:not([href]):focus {
  outline: none;
}
pre {
  margin-top: 0;
  margin-bottom: 1rem;
}
figure {
  margin: 0 0 1rem;
}
img {
  vertical-align: middle;
}
button,
[type="submit"],
[type="reset"],
[type="button"],
[role="button"] {
  cursor: pointer;
}
a,
area,
button,
[role="button"],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}
table {
  background-color: transparent;
}
caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #777;
  text-align: left;
  caption-side: bottom;
}
th {
  text-align: left;
}
label {
  display: inline-block;
  margin-bottom: 0.5rem;
}
button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}
input,
button,
select,
textarea {
  margin: 0;
  line-height: inherit;
  border-radius: 0;
}
textarea {
  resize: vertical;
}
fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}
legend {
  display: block;
  width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
}
input[type="search"] {
  -webkit-appearance: none;
}
output {
  display: inline-block;
}
[hidden] {
  display: none !important;
}
@media (min-width: 700px) {
  .guide-color-list .guide-color {
    width: 50%;
    float: left;
  }
}
@media (min-width: 1000px) {
  .guide-color-list .guide-color {
    width: 33.333%;
  }
}
.guide-color {
  margin-bottom: 1em;
}
.guide-color-swatch {
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  border: solid 1px rgba(0,0,0,0.1);
  border-radius: 0.2em;
  overflow: hidden;
}
.guide-color-tint {
  height: 0;
  padding-top: 0.75em;
  border-bottom: solid 1px rgba(0,0,0,0.1);
}
.guide-color-hex {
  text-transform: uppercase;
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  padding: 0.2em 0.5em;
}
.guide-color-title {
  display: inline-block;
  vertical-align: middle;
  font-size: 1.2em;
  margin-left: 1em;
}
body {
  cursor: default;
}
.guide {
  max-width: 1000px;
  margin: 0 auto;
}
.guide-head {
  text-align: center;
}
@media (min-width: 400px) {
  .guide {
    padding: 2em;
    padding-bottom: 4em;
  }
}
.section:not(:first-child) {
  margin-top: 2em;
  border-top: solid 1px #eee;
  padding-top: 1.5em;
}
.section-block:after {
  display: block;
  content: '';
  clear: both;
}
